
import Vue from "vue";
import axios from "axios";
import { bytesToSize, getOS, getIcon } from "@/apis/api";
export default Vue.extend({
  props: {
    filesSelectClose: Function,
    selectType: String,
  },
  watch: {},
  data() {
    return {
      selectShow:true,
      props: {
        label: "name",
        isLeaf: "isLeaf",
      },
      mountPoints: [] as any,
      dirList: [] as any,
      crumbArray: [] as any,
      fileNode: [] as any,
      fileList: [] as any,
      fileSelect: [] as any,
      isMac: new getOS().isMac,
      typeOptions: [{  value: '/jpg|jpeg|png|webp|gif|bmp|heic|tif|ico/',
                    label: this.$t("selection.imageFiles").toString() + ' (*.jpg; *.jpeg; *.png; *.webp; *.gif; *.bmp; *.heic; *.tif; *.ico)'
                }, 
                { value: false,
                    label: this.$t("selection.allFiles").toString()
                }, ],
      typeFilterSelected: "/jpg|jpeg|png|webp|gif|bmp|heic|tif|ico/",
    };
  },
  mounted() {
    // this.getMountPath(resolve);
  },
  computed: {},
  methods: {
    async loadNode(node: any, resolve: any) {
      console.log(node);
      // console.log(resolve)
      if(this.selectType == "dir"){
            const checkbox: HTMLElement = document.querySelector("#fileTable th.el-table__cell .cell .el-checkbox") as HTMLElement;
            if(checkbox){ checkbox.remove() }
          }
      if (node.level === 0) {
        
        this.fileList = node.childNodes;
        setTimeout(async () => {//.el-table-column--selection .cell
          // const checkbox: HTMLElement = document.querySelector(".el-table-column--selection .cell") as HTMLElement;
          // checkbox.style.display="none"; 
          
          const treeElement: HTMLElement = document.querySelector(".el-tree-node__content") as HTMLElement;
          treeElement.click();
        },0)
        
        //  await this.getMountPath(resolve);
        const level0Data: object = this.isMac ? { name: "macOS" } : { name: "Computer" }
        return resolve([level0Data]);
      }else if (node.level === 1) {
        await this.getMountPath(resolve);
      }else if (node.level > 1) {
        
        await this.getDir(node, resolve);
        //this.$refs.fileTree.filter('val');
        (this.$refs.fileTree as Vue & {filter: any}).filter(true); //filter unfolder by filterNode()
        if(node.childNodes.filter((element: any) => element.isLeaf == false).length < 1 ){
          node.isLeaf = true
        }        
        // return resolve([]);
      }
      this.fileNode = [...node.childNodes]
      if(this.selectType == "dir"){
        this.fileList = this.fileNode.filter((element: any) => element.data.isFolder == true);
      }else if(this.selectType == "file"){
        this.handleTypeFilter()
      }
      
    },
    async handleNodeClick(data: any, node: any) {
      if (node.level > 1) {
        this.crumbArray = data.fullPath.split(this.isMac ? "/" : "\\").filter(Boolean);
      }
      
      if (node.childNodes.length !== 0) {
        this.fileNode = [...node.childNodes]
        if(this.selectType == "dir"){
          this.fileList = this.fileNode.filter((element: any) => element.data.isFolder == true);
        }else if(this.selectType == "file"){
          this.handleTypeFilter()
        } 
      }
    },
    handleTypeFilter(){
      if(this.typeFilterSelected){
        this.fileList = this.fileNode.filter((element: any) => /jpg|jpeg|png|webp|gif|bmp|heic|tif|ico/.test(element.data.name.split('.')[element.data.name.split('.').length-1].toLowerCase()) == true);
      }else{
        this.fileList = this.fileNode.filter((element: any) => element.data.isFolder == false);
      }
    },
    filterNode(value: any, data: any, node: any){
      if(node.data.isFolder == value){
        return true
      }else{
        return false 
      }
    },
    handleSelectionChange(val:any) {
      this.fileSelect = val;
      console.log(this.fileSelect)
    },
    select(selection: any, row: any){
      if(this.selectType == "dir"){
        // this.$refs.fileTable.clearSelection()
        (this.$refs.fileTable as Vue & {clearSelection: any}).clearSelection() //clear selection
        
        if(selection.length == 0) return
        // this.$refs.fileTable.toggleRowSelection(row, true)
        (this.$refs.fileTable as Vue & {toggleRowSelection: any}).toggleRowSelection(row, true)
      }
      

    },
    clickSelect(){//father : to next step
      if(this.fileSelect.length > 0){
        this.$emit("dirSelectPath", this.fileSelect);
        this.filesSelectClose();
      }
    },
    alertClose(){//father : close pop
      this.filesSelectClose();
    },
    headerRowStyle(row: any, rowIndex: any) {
      // console.log(row.rowIndex)
      return "padding:4px 9px;";
    },
    headerCellClass(rowIndex: any, columnIndex: any) {
      return "headerCol " + "headerCol" + rowIndex.columnIndex;
    },
    async getMountPath(resolve: any) {
      let resData: any = {};
      await axios
        .post("/cgi/iBoysoftService_getMountPoints", { data: [0, 1] })
        .then((res) => {
          resData = res.data;
        });
      if (resData.b) {
        this.mountPoints = resData.data.mountpoints.map((element: any) => ({
          ...element,
          name: this.isMac ? element.name : element.path,
          fullPath: element.path + (this.isMac ? "/" : ""),
          isFolder: true,
        }));
        return resolve(this.mountPoints);
      }
    },
    async getDir(node: any, resolve: any) {
      let resData: any = {};
      let pathName: any = node.data.fullPath;
      await axios
        .post("/cgi/iBoysoftService_readDir", { data: [pathName, 1] })
        .then((res) => {
          resData = res.data;
        });
      if (resData.b) {
        // this.dirList = resData.data.files.map((element: any) => ({
        //   ...element,
        //   fullPath: pathName + element.name + (element.isFolder ? (this.isMac ? "/" : "\\") : ""),
        //   isLeaf: !element.isFolder,
        //   size : element.isFolder ? "-" : new bytesToSize().bytesToSize(element.size),
        // }));
        this.dirList = resData.data.files 
        this.dirList.forEach( async (element: any) => {
          element.fullPath = pathName + element.name + (element.isFolder ? (this.isMac ? "/" : "\\") : "")
          element.isLeaf = !element.isFolder
          element.size = element.isFolder ? "-" : new bytesToSize().bytesToSize(element.size)
          element.image = await new getIcon().getIcon(element.fullPath)
        });
        return resolve(this.dirList);
      }
    },
  },
});
