import axios from "axios";

/*
容量换算
*/
export class bytesToSize {
  bytesToSize(bytes: number) {
    if (bytes === 0) return '0 B';
    const k = /macintosh|mac os x/i.test(navigator.userAgent) ? 1000 : 1024,
      sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k));
    return (bytes / Math.pow(k, i)).toFixed(2) + ' ' + sizes[i];
  }
}

/*
判断Mac os
*/ 
export class getOS{
  isMac: boolean = /macintosh|mac os x/i.test(navigator.userAgent);
}

/*
判断 支持 webp
*/ 
export class supportWebp {
  supportWebp() {
    const elem = document.createElement('canvas');
    if ((elem.getContext && elem.getContext('2d'))) {
      // was able or not to get WebP representation
      return elem.toDataURL('image/webp').indexOf('data:image/webp') == 0;
    } else {
      // very old browser like IE 8, canvas not supported
      return false;
    }
  }
}

/*
获取路径是否为文件夹
*/
export class pathIsFolder {
  async pathIsFolder(path: string) {
    let resTaskData: any = { b: false, msg: '', data: '' };
    await axios
      .post("/cgi/iBoysoftService_pathIsFolder", { data: [path] }, { headers: {'Content-type': 'application/json; charset=utf-8'} })
      .then((res) => {
        resTaskData = res.data;
      })
      .catch( e =>{
        console.log(e)
    })
    if(resTaskData){
        return { b: resTaskData.b, msg: resTaskData.msg, data: resTaskData.data };
    }
  }
}

/*
获取文件图标
*/
export class getIcon {
  async getIcon(path: string) {
    let resTaskData: any = { b: false, msg: '', data: '' };
    await axios
      .post("/cgi/iBoysoftService_getIcon", { data: [path] }, { headers: {'Content-type': 'application/json; charset=utf-8'} })
      .then((res) => {
        resTaskData = res.data;
      })
      .catch( e =>{
        console.log(e)
    })
    if(resTaskData){
        return  "data:image/png;base64," + resTaskData.data ;
    }
  }
}